  // Êõ¥Êñ∞Ëá™ÂÆö‰πâÂ≠óÊÆµ
  const updateCustomField = (index: number, field: Partial<CustomFieldDefinition>) => {
    const newFields = [...customFields];
    newFields[index] = { ...newFields[index], ...field };
    setCustomFields(newFields);
  };

